export default function Map(province) {
  switch (province) {
    case 'south-africa': return southAfrica;
    case 'western-cape': return westernCape;
    case 'eastern-cape': return easternCape;
    case 'northern-cape': return northernCape;
    case 'free-state': return freeState;
    case 'north-west': return northWest;
    case 'kwazulu-natal': return kwaZuluNatal;
    case 'gauteng': return gauteng;
    case 'limpopo': return limpopo;
    case 'mpumalanga': return mpumalanga;
    default: return null;
  }
}



